  /* Result Component */
  .result {
    /* padding: 1.5rem 2.5rem; */
    background-color: #FAF7EF;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  .resultBox {
    width: 720px;
    height: 727px;
    background-color: white;
    box-shadow: 0px 0px 10px #D2C4A8;
    margin: 0;
    border-radius: 3px;
  }

  .title {
    background-color: #FAF7EF;
    margin-top: 30px;
  }
  .quiz {
    background-color: #FAF7EF;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  .quizBox {
    width: 720px;
    height: 727px;
    background-color: white;
    box-shadow: 0px 0px 10px #D2C4A8;
    margin: 0;
    border-radius: 3px;
  }
  .bear {
    width: 200px;
    position: absolute;
    top: 50%;
    left: 0%;
    -ms-transform: translate(50%, 50%);
    transform: translate(-140%, -50%);
  }
  .gorilla {
    width: 200px;
    position: absolute;
    top: 50%;
    left: 100%;
    -ms-transform: translate(50%, 50%);
    transform: translate(40%, -50%);
  }

  /* Quiz Component */
  .answerOptions {
    margin: 0;
    padding: 0;
    list-style: none;
    flex-direction: column;
    align-items:center;
  }
  
  /* QuestionCount Component */
  .questionCount {
    padding-top: 30px;
    font-size: 20px;
    color: #d6b064;
  }
  
  /* Question Component */
  .question {
    margin: 0;
    padding-left: 40px;
    padding-right: 40px;
    margin-top: 70px;
    margin-bottom: 30px;
    font-family: 'TiemposHeadline';
    font-size: 40px;
  }
  
  /* AnswerOption Component */
  .answerOption {
    /* border-top: 1px solid #eee; */
    align-items:left;
    width: 80%;
    margin-right: 0;
  }
  
  .answerOption:hover {
    background-color: #f4ead74D;
  }
  
  .radioCustomButton {
    position: absolute;
    width: auto;
    opacity: 0;
  }
  
  .radioCustomButton,
  .radioCustomLabel {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
  }
  
  .radioCustomLabel {
    position:relative;
    width: 100%;
    margin: 0;
    padding: 1.5rem 2.5rem 1.5rem 5rem;
    font-size: 20px;
    line-height: 1.2;
    text-align: left;
  }
  
  .radioCustomButton ~ .radioCustomLabel:before {
    position: absolute;
    top: 20px;
    left: 38px;
    width: 28px;
    height: 28px;
    content: '';
    display: inline-block;
    vertical-align: middle;
    background: #fff;
    border: 1px solid #bbb;
    border-radius: 50%;
    transition: all 0.3s;
  }
  
  .radioCustomButton:checked ~ .radioCustomLabel:before {
    content: '';
    background-size: 27px;
    border-color: #d6b064;
    background-color: #d6b064;
  }
  
  /* Animation */
  .fade-enter {
    opacity: 0;
  }
  
  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 0.5s ease-in-out 0.3s;
  }
  
  .fade-leave {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 1;
  }
  
  .fade-leave.fade-leave-active {
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
  }
  
  .fade-appear {
    opacity: 0;
  }
  
  .fade-appear.fade-appear-active {
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
  }